import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as products from "./products.json";

@Component({
  selector: 'udb-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit, OnDestroy {

  product:any;
  subscriptions:any={}

  constructor(private route: ActivatedRoute, private router: Router) { }

  getProduct(){
    let productName=this.route.snapshot.params['product'];
    this.product=(<any>products).products[productName];
  }

  ngOnInit() {
    this.getProduct();
    this.subscriptions.re=this.router.events.subscribe((event:any)=>{
      if(event.url) {
        this.getProduct();
      }
    });
  }

  ngOnDestroy(){
    this.subscriptions.re.unsubscribe();
  }

}
